let that;
export default {
	data() {
		return {
			fristEle: [{
					name: '数据备份',
					notice: '敬请期待',
					icon: 'cloud-upload',
					id: 0,
				},
				{
					name: '账号注销',
					notice: '敬请期待',
					icon: 'telegram',
					id: 1
				},
				{
					name: '系统重建',
					notice: '敬请期待',
					icon: 'link',
					id: 2
				}
			],
			
			secondEle: [
				{
					name: '导入模板',
					notice: '查看->导入',
					icon: 'cloud-upload',
					url:"/pages/mine/uploadFile/uploadFile",
					id:3
				},
				{
					name: '导入商品资料',
					notice: '会员可用',
					icon: 'cloud-upload',
					url:"/pages/mine/uploadFile/uploadFile",
					id:4
				}
			],

			nowFristEle: [],
			nowSecondEle:[],
			user: uni.getStorageSync("user"),
		};
	},

	mounted() {
		that = this;
		if (that.user.identity == 2) {
			that.nowFristEle = []
			that.nowSecondEle = []
		}else{
			that.nowFristEle = that.fristEle
			that.nowSecondEle = that.secondEle
		}
	},
	
	methods:{
		options(id){
			if(id == 3){
				that.downloadDemoFile()
			}
			
			if(id == 4){
				that.uploadfile()
			}
		},
		
		//下载数据模板
		downloadDemoFile(){
			wx.downloadFile({
			  // 示例 url，并非真实存在
			  url: 'https://www.jimuzhou.com/static/demo.xlsx',
			  success: function (res) {
			    const filePath = res.tempFilePath
			    wx.openDocument({
			      filePath: filePath,
						fileType:'xlsx',
			      success: function (res) {
			        console.log('打开文档成功')
			      }
			    })
			  }
			})
		},
		
		uploadfile() {
			if(that.user.is_vip){
				uni.showLoading({
					title:"上传中..."
				})
				wx.chooseMessageFile({
					count: 1,
					type: 'file',
					success(res) {
						console.log(res)
						// tempFilePath可以作为img标签的src属性显示图片
						const tempFiles = res.tempFiles
				
						uni.uploadFile({
							url: 'https://www.jimuzhou.com/api/getfile.php', //仅为示例，非真实的接口地址
							filePath: tempFiles[0].path,
							name: 'file',
							formData: {
								'userid': uni.getStorageSync('uid')
							},
							success: (uploadFileRes) => {
								//console.log(JSON.parse(uploadFileRes.data))
								let result = JSON.parse(uploadFileRes.data)
								uni.hideLoading();
								if(result.code == "1"){
									uni.showToast({
										title:"上传成功"
									})
								}
							}
						});
					}
				})
			}else{
				uni.showToast({
					title:"您还不是会员",
					icon:"none"
				})
			}
		},
		
	}
}
